		data() {
			return {
				StatusBar: this.StatusBar,
				CustomBar: this.CustomBar,
				menuBorder: false,
				menuArrow: false,
				menuCard: false,
				id:'',
				name:'李白',
				tel:'17777777777',
				nackName:'111',
				sex:'男',
				email:'123@qq.com',
				qq:'17777777',
				area:'北京市-市辖区-东城区',
				address:'北京市-市辖区-东城区',
				list:[],
			};
		},
		onLoad() {
			const value = uni.getStorageSync('agentInfo');
			if (value) {
				this.id=value.id;
			}
			uni.request({
				url: 'http://192.168.0.199:8080/agent/agent/ajax-agent-info',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				method: 'POST',
				dataType: 'json',
				cache: false,
				data: {
					token:this.token,
					id:this.id,
				},
				success: res => {
					this.list=res;
					let data=this.list.data;
					console.log(data);
					if(data.isSuccess==200){
						this.name=data.result.agent_name;
						this.tel=data.result.agent_tel;
						this.nackName=data.result.nickname;
						if(data.result.sex=='0'){
							this.sex='男';
						}else if(data.result.sex=='1'){
							this.sex='女';
						}
						this.email=data.result.email;
						this.qq=data.result.qq;
						this.area=data.result.area;
						this.address=data.result.address;
					}
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				},
				complete: () => {}
				});
		},
		methods: {
			ToPage: function() {
					uni.redirectTo({
						url: '../about/editinfo'
					});
			},
			navTo:function(){
				uni.redirectTo({
					url: '../tabbar/tabbar'
				});
			}
		}
	}
